.table {
  width: 40vw;
}

.tableHeader {
  display: grid;
  grid-template-columns: 1fr 2fr 1fr 1fr 1fr 2fr;

  font-family: 'DM Sans', sans-serif;
  font-size: 14px;
  line-height: 20px;
  color: #000000;
  mix-blend-mode: normal;
  opacity: 0.4;

  text-align: center;
}

.tableHeader span:nth-child(1) {
  grid-column: 2 / span 1;
}

.tableHeader span:nth-child(2) {
  grid-column: 3 / span 1;
}

.tableHeader span:nth-child(3) {
  grid-column: 4 / span 1;
}

.tableHeader span:nth-child(4) {
  grid-column: 5 / span 1;
}

.tableHeader span:nth-child(5) {
  grid-column: 6 / span 1;
}

.tableBody {
  padding: 10px;
  border-radius: 10px;
  background-color: white;
  font-family: 'DM Sans', sans-serif;
  font-size: 14px;
  line-height: 20px;
}

.tableRow {
  display: grid;
  grid-template-columns: 1fr 2fr 1fr 1fr 1fr 2fr;
}

.tableRow img {
  grid-column: 1 / span 1;
}

.tableRow span {
  display: inline-block;
  text-align: center;
  align-self: center;
}

.tableRow span:nth-of-type(1) {
  grid-column: 2 / span 1;
  text-align: left;
}

.tableRow span:nth-of-type(2) {
  grid-column: 3 / span 1;
  mix-blend-mode: normal;
  opacity: 0.4;
}

.tableRow span:nth-of-type(3) {
  grid-column: 4 / span 1;
  mix-blend-mode: normal;
  opacity: 0.4;
}

.tableRow span:nth-of-type(4) {
  grid-column: 5 / span 1;
  mix-blend-mode: normal;
  opacity: 0.4;
}

.tableRow span:nth-of-type(5) {
  grid-column: 6 / span 1;
  text-align: right;
  font-weight: 500;
  font-size: 22px;
  line-height: 29px;
}

.asset {
  color: #34B53A;
}

.debt {
  color: #FF3A29;
}

.tableEmpty {
  display: block;

  font-size: 2em;
  line-height: 4em;
  text-align: center;

  color: #000000;
  mix-blend-mode: normal;
  opacity: 0.4;
}
