.container {
  background-color: #4339F2;
  border-radius: 10px;
  color: white;
  font-family: 'DM Sans', sans-serif;
  padding: 1em 2em;
  display: flex;
  flex-direction: row;
  align-items: center;
}

.info {
  margin-left: 3em;
  flex-grow: 1;
}

.button {
  justify-self: end;
  align-self: flex-end;
}