.button {
  border: none;
  outline: none;
  background-color: #4339F2;
  border-radius: 10px;
  height: 3em;
  width: 10em;
  cursor: pointer;

  color: white;
  font-family: 'DM Sans', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 20px;
  text-align: center;
}

.buttonInverted {
  background-color: white;
  color: #4339F2;
  border: 1px solid;
  border-color: #4339F2;
}