.container {
  display: flex;
  flex-direction: column;
  background-color: white;

  font-family: 'DM Sans', sans-serif;
  color: black;

  width: 30em;
  border-radius: 10px;
  padding: 2em;
}

.field {
  outline: none;
  border: 1px solid #F2F2F2;
  border-radius: 10px;
  font-family: 'DM Sans', sans-serif;
  font-size: 14px;
  line-height: 20px;
  height: 3em;
  margin: 0.5em 0;
  box-sizing: border-box;
  padding: 0 10px;
}

.field::placeholder {
  color: rgba(0, 0, 0, 0.25);
}

.container select:required:invalid {
  color: rgba(0, 0, 0, 0.25);
}

.container select:last-of-type {
  margin-bottom: 1em;
}

.submit {
  align-self: center;
}